@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap");
:root {
  --active-color: hsl(220, 98%, 61%);
  --linear-gradient: 135deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%);
}

.dark-theme {
  --background-color: hsl(235, 21%, 11%);
  --todo-bg: hsl(235, 24%, 19%);
  --checkbox-color: hsl(237, 14%, 26%);
  --text-color-active: hsl(234, 39%, 85%);
  --color-text-completed: hsl(233, 14%, 35%);
  --button-hover: hsl(236, 33%, 92%);
  --color-text-1: hsl(234, 11%, 52%);
}

.light-theme {
  --background-color: hsl(0, 0%, 98%);
  --todo-bg: hsl(0, 0%, 100%);
  --checkbox-color: hsl(236, 33%, 92%);
  --text-color-active: hsl(235, 19%, 35%);
  --color-text-completed: hsl(233, 11%, 84%);
  --color-text-1: hsl(236, 9%, 61%);
}

.header {
  padding: 2.8125rem 1.5625rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 33.75rem;
  margin: auto;
}
.header__title {
  font-size: 1.625rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.4375rem;
}
.header-bg-toggler {
  background: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  position: relative;
  width: 1.5625rem;
  height: 1.5625rem;
}
.header-bg-toggler:focus {
  outline: 0.125rem double var(--color-text-completed);
  outline-offset: 0.125rem;
}
.header-bg-toggler .sun-img,
.header-bg-toggler .moon-img {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none;
  transition: all 500ms ease-in-out;
}
.header-bg-toggler .sun-img:hover,
.header-bg-toggler .moon-img:hover {
  transform: scale(1.2);
}

.main {
  width: 100%;
  padding: 0 1.5625rem;
  max-width: 33.75rem;
  margin: auto;
}

.form {
  padding: 0.9375rem 1.25rem;
  background: var(--todo-bg);
  border-radius: 0.3125rem;
  margin-bottom: 1.25rem;
}
.form label {
  position: relative;
}
.form label::before {
  content: "";
  width: 1.5625rem;
  height: 1.5625rem;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  position: absolute;
  border: 0.0625rem solid var(--checkbox-color);
  border-radius: 50%;
}
.form .todo-input {
  margin-left: 2.5rem;
  background: transparent;
  border: none;
  outline: none;
  color: var(--text-color-active);
  letter-spacing: 0.0625rem;
  width: calc(100% - 2.5rem);
}
.form .todo-input::placeholder {
  font-size: 0.8125rem;
  color: var(--color-text-1);
  background: inherit;
}

.todo-container {
  background: var(--todo-bg);
  border-radius: 0.3125rem;
  overflow: hidden;
  box-shadow: 0 0.375rem 1.5625rem -0.75rem #000000;
}
.todo-container .todo-list {
  list-style-type: none;
}
.todo-container .todo-list .todo-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.9375rem 1.25rem;
  border-bottom: 0.0625rem solid var(--checkbox-color);
}
.todo-container .todo-list .todo-item.disappear {
  transform: translateX(-2000%);
  position: absolute;
}
.todo-container .todo-list .todo-item.selected {
  background: rgba(192, 88, 243, 0.1);
}
.todo-container .todo-list .todo-item.highlight {
  background: rgba(87, 221, 255, 0.1);
}
.todo-container .todo-list .todo-item:hover .todo-delete-button {
  opacity: 1;
}
.todo-container .todo-list .todo-item:hover .todo-check {
  border-color: transparent;
}
.todo-container .todo-list .todo-item.completed .todo-label .todo-text {
  color: var(--color-text-completed);
}
.todo-container .todo-list .todo-item.completed .todo-label .todo-text::after {
  transform: translateX(0);
  width: 100%;
  opacity: 1;
}
.todo-container .todo-list .todo-item .todo-item-input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 0;
  width: 0;
}
.todo-container .todo-list .todo-item .todo-item-input:focus + .todo-label .todo-check {
  outline: 0.125rem double var(--color-text-completed);
  outline-offset: 0.125rem;
}
.todo-container .todo-list .todo-item .todo-item-input:checked + .todo-label .todo-check {
  background: linear-gradient(var(--linear-gradient));
  border: 0.0625rem;
  border-color: transparent;
}
.todo-container .todo-list .todo-item .todo-item-input:checked + .todo-label .todo-check::after {
  opacity: 1;
}
.todo-container .todo-list .todo-item .todo-item-input:checked + .todo-label .todo-check::before {
  opacity: 0;
}
.todo-container .todo-list .todo-item .todo-label {
  display: flex;
  align-items: center;
  height: 100%;
  cursor: pointer;
  position: relative;
  max-width: 90%;
}
.todo-container .todo-list .todo-item .todo-label .todo-text {
  color: var(--text-color-active);
  margin: 0 0.625rem 0 2.1875rem;
  font-size: 0.875rem;
  position: relative;
  overflow: hidden;
  transition: 500ms ease-in-out;
  word-break: break-word;
  letter-spacing: 0.0625rem;
}
.todo-container .todo-list .todo-item .todo-label .todo-text::after {
  position: absolute;
  content: "";
  width: 0;
  height: 0.0625rem;
  background: var(--color-text-completed);
  left: 0;
  top: 50%;
  transform: translateX(-100%);
  opacity: 0;
  transition: 500ms ease-in-out;
}
.todo-container .todo-list .todo-item .todo-label .todo-check {
  position: absolute;
  top: 50%;
  left: 0;
  height: 1.5625rem;
  width: 1.5625rem;
  border: 0.0625rem solid var(--checkbox-color);
  border-radius: 50%;
  transform: translateY(-50%);
  transition: border 500ms;
}
.todo-container .todo-list .todo-item .todo-label .todo-check:hover {
  background: linear-gradient(var(--linear-gradient));
}
.todo-container .todo-list .todo-item .todo-label .todo-check::before {
  content: "";
  position: absolute;
  top: 0.0625rem;
  left: 0.0625rem;
  width: calc(100% - 0.125rem);
  height: calc(100% - 0.125rem);
  background: var(--todo-bg);
  border: double 0.0625rem transparent;
  border-radius: 50%;
  opacity: 1;
  transition: 500ms ease-in-out;
}
.todo-container .todo-list .todo-item .todo-label .todo-check::after {
  content: url(../../../images/icon-check.svg);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  opacity: 0;
  transition: 500ms ease-in-out;
}
.todo-container .todo-list .todo-item .todo-delete-button {
  width: 1.25rem;
  height: 1.25rem;
  background: transparent;
  opacity: 0;
  transition: 350ms;
}
.todo-container .todo-list .todo-item .todo-delete-button:focus {
  opacity: 1;
  outline: 0.125rem double var(--color-text-completed);
  outline-offset: 0.125rem;
}
.todo-container .todo-list .todo-item .todo-delete-button .todo-delete-button-image {
  height: 100%;
  width: 100%;
  pointer-events: none;
}

.counter-clear {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.9375rem 1.25rem;
  background: var(--todo-bg);
  font-size: 0.8125rem;
  color: var(--color-text-1);
}

.clear-completed {
  background: transparent;
  color: var(--color-text-1);
  transition: 250ms ease-in-out;
}
.clear-completed:hover {
  color: var(--text-color-active);
}
.clear-completed:focus {
  outline: 0.125rem double var(--color-text-completed);
  outline-offset: 0.125rem;
}

.todo-filter {
  padding: 0.9375rem 1.25rem;
  background: var(--todo-bg);
  border-radius: 0.3125rem;
  margin-top: 1.25rem;
}

.filter-btns {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  max-width: 12.5rem;
  margin: auto;
}
.filter-btns button {
  background: transparent;
  font-size: 1rem;
  font-weight: bold;
  color: var(--color-text-1);
  transition: 250ms ease-in-out;
}
.filter-btns button.active {
  color: var(--active-color);
}
.filter-btns button:hover {
  color: var(--text-color-active);
}
.filter-btns button:focus {
  outline: 0.125rem double var(--color-text-completed);
  outline-offset: 0.125rem;
}

.sm-hidden {
  display: none;
}
@media (min-width: 40em) {
  .sm-hidden {
    display: flex;
  }
}

.md-hidden {
  display: flex;
}
@media (min-width: 40em) {
  .md-hidden {
    display: none;
  }
}

.info {
  display: block;
  text-align: center;
  margin-top: 3.125rem;
  color: var(--color-text-1);
}

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Josefin Sans", sans-serif;
}

a {
  text-decoration: none;
}
a:focus {
  outline: 2px double var(--color-text-completed);
  outline-offset: 2px;
}

body {
  min-height: 100vh;
  background: var(--background-color);
  position: relative;
  font-size: 1.125rem;
  transition: 1s;
}
body::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 14.375rem;
  z-index: -1;
  transition: 1s;
}
body.dark-theme::before {
  background: url(../../../images/bg-mobile-dark.jpg) center/cover no-repeat;
}
body.light-theme::before {
  background: url(../../../images/bg-mobile-light.jpg) center/cover no-repeat;
}
@media (min-width: 40em) {
  body.dark-theme::before {
    background: url(../../../images/bg-desktop-dark.jpg) center/cover no-repeat;
  }
  body.light-theme::before {
    background: url(../../../images/bg-desktop-light.jpg) center/cover no-repeat;
  }
}
body.dark-theme header .header-bg-toggler .sun-img {
  opacity: 1;
  pointer-events: initial;
}
body.light-theme header .header-bg-toggler .moon-img {
  opacity: 1;
  pointer-events: initial;
}

ul {
  list-style-type: none;
}

button {
  border: none;
  outline: none;
  cursor: pointer;
}